<template lang="">
  <div class="sign-up flex justify-center flex-wrap">
    <p class="basis-full text-center">회원가입</p>
    <input v-model="email" type="text" placeholder="email" class="border"><br>
    <input v-model="password" type="password" placeholder="password" class="border"><br>
    <input v-model="nickname" type="text" placeholder="닉네임" class="border"><br>
    <button @click="signUp">가입하기</button>
  </div>
</template>
<script>
import {auth} from "../firebase"

export default {
  name:"MemberView",
  data() {
    return {
      email:"",
      password:"",
      nickname:"",
    }
  },
  methods:{
    signUp(){
      auth.createUserWithEmailAndPassword(this.email, this.password).then((result)=>{
        result.user.updateProfile({displayName: this.nickname})
      })
    }
  }
}
</script>
