<template lang="">
  <div>
    읽기 
    {{BoardContent.title}}
    {{BoardContent.name}}
    
  </div>
</template>

<script>
import {db} from "../../firebase"
export default {
  name:"NoticeRead",
  data() {
    return {
      BoardContent : []
    }
  },
  mounted(){
    if(this.$store.state.noticeId == 0){
      this.$router.replace("/cs/notice")
    }
    db.collection("notice").doc(this.$store.state.noticeId).get().then((data)=>{
      this.BoardContent = data.data()
    })
  }
}
</script>
