<template lang="">
  <div class="basis-full">
    <div class="max-w-7xl mx-auto my-10">
      <ul class="flex justify-between border-t-indigo-500 border-t-2  py-2 bg-indigo-50 text-xs sm:text-sm text-center">
        <li class="basis-20">번호</li>
        <li class="basis-6/12">제목</li>
        <li class="basis-24">글쓴이</li>
        <li class="basis-16">날짜</li>
      </ul>
      <ul v-for="(e , index) in dataList.slice().reverse()" :key="index" class="flex justify-between border-b text-center py-2 even:bg-gray-50 text-xs sm:text-sm">
        <li class="basis-20">{{dataList.length - index}}</li>
        <li class="basis-6/12"><router-link to="/cs/notice/read" @click="$store.commit('NoticeRead', dataId[dataList.length - (index+1)])">{{e.title}}</router-link></li>
        <li class="basis-24">{{e.name}}</li>
        <li>{{BoardDate(index)}}</li>
      </ul>
    </div>

    <div class="flex justify-end pb-24">
      <router-link to="/cs/notice/write" class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:right-2 focus:ring-opacity-75 text-xs sm:text-sm">글쓰기</router-link>
    </div>
  </div>
</template>
<script>
import {db} from "../../firebase"
export default {
  name:"NoticeList",
  data() {
    return {
      dataList : [] ,
      dataId : []
    }
  },
  methods:{
    BoardDate(index){
      const date = this.dataList[index].date.seconds * 1000 + this.dataList[index].date.nanoseconds / 1000000
      // 밀리세컨트초를 date 객체로 변환
      const new_date = new Date(date);
      const formattedDate = (''+new_date.getFullYear()).slice(-2)+'-' 
      + ('0'+ (new_date.getMonth() + 1)).slice(-2) + '-'
      + ('0'+ new_date.getDate()).slice(-2) + ''
      return formattedDate 
    }
  },
  mounted(){
    db.collection("notice").get().then((data)=>{
      data.forEach((e)=>{
        this.dataId.push(e.id)
        this.dataList.push(e.data())
        console.log(this.dataList)
      })
    })
  }
}
</script>
