import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginVue from '../views/LoginView.vue'
import MemberVue from '../views/MemberView.vue'
import CompanyVue from '@/views/CompanyView.vue'
import GreetingsVue from '@/views/Company/GreetingsView.vue'
import HistoryVue from '@/views/Company/HistoryView.vue'
import InteriorVue from '@/views/Company/InteriorView.vue'
import DirectionsVue from '@/views/Company/DirectionsView.vue'


import BusinessVue from '@/views/BusinessView.vue'
import BusinessVue_1 from '@/views/Business/BusinessView_1.vue'
import BusinessVue_2 from '@/views/Business/BusinessView_2.vue'
import BusinessVue_3 from '@/views/Business/BusinessView_3.vue'

import ProductVue from '@/views/ProductView.vue'
import ProductVue_1 from '@/views/Product/ProductView_1.vue'
import ProductVue_2 from '@/views/Product/ProductView_2.vue'
import ProductVue_3 from '@/views/Product/ProductView_3.vue'

import CsVue from '@/views/CsView.vue'
import NoticeVue from '@/views/Cs/NoticeView'
import NoticeList from '@/views/Cs/NoticeList'
import NoticeRead from '@/views/Cs/NoticeRead'
import NoticeModify from '@/views/Cs/NoticeModify'
import NoticeWrite from '@/views/Cs/NoticeWrite'



import OnlineVue from '@/views/Cs/OnlineView'
import QnaVue from '@/views/Cs/QnaView'
import GalleryVue from '@/views/Cs/GalleryView'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: "LoginView",
    component :LoginVue
  },
  {
    path: "/member",
    name: "MemberView",
    component: MemberVue
  },
  {
    path: "/company",
    name: "CompanyView",
    component: CompanyVue,
    redirect: "/company/greetings",
    children: [
      {
        path: "/company/greetings",
        name: "GreetingsView",
        component: GreetingsVue
      },
      {
        path: "/company/history",
        name: "HistoryView",
        component: HistoryVue
      },
      {
        path: "/company/interior",
        name: "InteriorView",
        component: InteriorVue
      },
      {
        path: "/company/directions",
        name: "DirectionsView",
        component: DirectionsVue
      }
    ]
  },
  {
    path: "/business",
    name: "BusinessView",
    component: BusinessVue,
    redirect: "/business/business-1",
    children: [
      {
        path: "/business/business-1",
        name: "BusinessView_1",
        component: BusinessVue_1
      },
      {
        path: "/business/business-2",
        name: "BusinessView_2",
        component: BusinessVue_2
      },
      {
        path: "/business/business-3",
        name: "BusinessView_3",
        component: BusinessVue_3
      },
    ]
  },
  {
    path: "/product",
    name: "ProductView",
    component: ProductVue,
    redirect: "/product/product-1",
    children: [
      {
        path: "/product/product-1",
        name: "ProductView_1",
        component: ProductVue_1
      },
      {
        path: "/product/product-2",
        name: "ProductView_2",
        component: ProductVue_2
      },
      {
        path: "/product/product-3",
        name: "ProductView_3",
        component: ProductVue_3
      },
    ]
  },
  {
    path: "/cs",
    name: "CsVue",
    component: CsVue,
    redirect: "/cs/notice",
    children: [
      {
        path: "/cs/notice",
        name: "NoticeView",
        component: NoticeVue,
        redirect: "/cs/notice/list",
        children: [
          {
            path: "/cs/notice/list",
            name: "NoticeList",
            component: NoticeList,
          },
          {
            path: "/cs/notice/write",
            name: "NoticeWrite",
            component: NoticeWrite,       
          },
          {
            path: "/cs/notice/read",
            name: "NoticeRead",
            component: NoticeRead,
          },
          {
            path: "/cs/notice/modify",
            name: "NoticeModify",
            component: NoticeModify,
          }
        ]
      },
      {
        path: "/cs/online",
        name: "OnlineView",
        component: OnlineVue
      },
      {
        path: "/cs/qna",
        name: "QnaView",
        component: QnaVue
      },
      {
        path: "/cs/gallery",
        name: "GalleryView",
        component: GalleryVue
      }
    ]
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
