<template lang="">
  <div class="basis-full">
    <div class="max-w-7xl mx-auto my-10">
      <input v-model="name" type="text" class="border" placeholder="이름">
      <input v-model="title" type="text" class="border" placeholder="제목">
      <textarea v-model="content" class="border"></textarea>
      <button class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:right-2 focus:ring-opacity-75 text-xs sm:text-sm" @click="write">완료</button>
    </div>
  </div>
</template>
<script>
import {db} from "../../firebase"
export default {
  name:"NoticeWrite",
  data() {
    return {
      title: "",
      content:"",
      name: this.$store.state.displayName,
      date: new Date()
    }
  },
  methods:{
    write(){
      db.collection("notice").add({
        "name": this.name,
        "title" : this.title,
        "content": this.content,
        "date": this.date
      })
      this.$router.replace("/cs/notice")
    }
  }
}
</script>
