<template>
  <Nav/>
  <router-view/>
  <Footer/>
</template>
<script>
import {auth} from "./firebase"
import Nav from './components/NavCom.vue';
import Footer from './components/FooterCom.vue'
export default {
  name: "AppPage",
  components:{
    Nav,
    Footer
  },
  mounted(){
    auth.onAuthStateChanged((user)=>{
      console.log(user)
      if(user){
        this.$store.commit('loginState', user.displayName);
      }
    })
  }
}
</script>
